import react, {Component} from 'react';
import {Navbar, NavbarBrand, NavbarItem, NavbarMenu, NavbarStart} from 'bloomer';



const myNav={
    border: 'solid, 1px, red',
    borderRadius: '0.75%',
    backgroundColor: '#303F9F',
    color: 'white'
}
const mytext={
    color: 'white'
}


export default class navbar extends Component{
  
    render(){
        return(<Navbar style={{border: 'solid, 1px, red'}}>
            <NavbarBrand>
                <NavbarItem style={mytext}>
                    Football coin
                </NavbarItem>
            </NavbarBrand>
            <NavbarMenu>
                <NavbarStart>
                    <NavbarItem  style={mytext}>
                        Home
                    </NavbarItem>
                </NavbarStart>
            </NavbarMenu>
        </Navbar>)
    }
}


